:export {
  grid_size: 20; }

:export {
  node_spacing: 13; }

:export {
  node_padding: 3; }

:export {
  background-size: 13; }

.group {
  padding-bottom: 0 !important;
  margin-bottom: 0px; }

textarea:focus,
input:focus {
  outline: none; }

.divider {
  padding-left: 10px;
  padding-right: 10px;
  vertical-align: sub;
  font-size: 13px;
  line-height: 13px;
  padding-top: 7px; }

.kase.has_intent .choice,
.kase.has_top_intent .choice {
  width: 150px; }

.kase.has_intent .multi_operand,
.kase.has_top_intent .multi_operand {
  width: 202px; }

.kase {
  display: inline-flex;
  width: 100%;
  height: 25px;
  padding-top: 15px;
  user-select: none; }
  .kase .form_group {
    margin: 0; }
  .kase .choice {
    width: 185px; }
  .kase .single_operand {
    display: inline-flex;
    position: relative;
    margin-left: 10px;
    width: 170px; }
    .kase .single_operand input {
      width: 170px; }
  .kase .multi_operand {
    display: inline-flex;
    margin-left: 10px;
    width: 170px; }
    .kase .multi_operand input[name='intent'] {
      width: 105px; }
    .kase .multi_operand input[name='confidence'] {
      padding: 10px 6px !important; }
  .kase .categorize_as {
    font-size: 13px;
    line-height: 13px;
    position: relative;
    top: 7px;
    width: 79px;
    margin: 0px 8px; }
  .kase .category {
    position: relative; }
    .kase .category input {
      width: 155px; }
  .kase:hover .remove_icon {
    opacity: 1; }
  .kase:hover .dnd_icon {
    opacity: 1; }
  .kase input {
    padding: 6px 7px;
    height: 28px !important;
    font-size: 13px !important;
    border: 1px solid #ccc; }
  .kase .relative_date {
    display: inline-block;
    width: 50px; }
    .kase .relative_date input {
      width: 50px; }

.dnd_icon {
  color: #aaa;
  font-weight: bold;
  padding-top: 9px;
  padding-right: 2px;
  padding-left: 5px;
  float: left;
  opacity: 0;
  font-size: 9px;
  line-height: 9px; }

.remove_icon {
  color: #aaa;
  padding-top: 8px;
  float: right;
  opacity: 0;
  line-height: 12px;
  cursor: pointer;
  margin-left: 2px;
  font-size: 9px;
  z-index: 3000; }

.empty {
  display: inline-block;
  width: 15px; }

[data-draggable='true'] {
  cursor: pointer; }
